
.resize-drag {
  background-color: rgba(3, 93, 206, 0.5);
  color: white;
  font-size: 20px;
  font-family: sans-serif;
  border-radius: 8px;
  min-height: 40px;
  min-width: 50px;
  width: 150px;
  height: 40px;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  cursor: move;
}
.text-container {
  text-align: center;
  height: 100%;
}
.text-container span {
  display: block;
  position: relative;
  top: 50%;
  font-size: 12px;
  width: 90%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



