
.nav {
  background-color: white;
}
.nav-title {
  display: block;
  padding: 10px 12px;
  font-size: 16px;
  line-height: 18px;
  color: #676767;
  background: #efefef;
  position: relative;
}
.nav-title::after {
  content: '';
  position: absolute;
  width: 10%;
  height: 0px;
  background: #cccccc;
  bottom: 0;
  left: 12px;
  margin: auto;
}



